.page.flag-nerd-hard {
	flex-direction: column;
	align-items: center;

	h1 {
		margin-bottom: 1rem;
	}

	.flag-container {
		margin-top: 1rem;
		margin-bottom: 2rem;
		height: 10rem;
		width: 15rem;

		.flag {
			height: 100%;
			width: 100%;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;
			border: none;
		}
	}

	.answer-container {

	}

	.input-container {
		display: flex;
		justify-content: center;
		gap: 1rem;
	}

	.answer-input {
		padding: 0 2rem;
		border: none;
		outline: none;
		border-radius: 1000px;
		font-size: 1.2rem;
		height: 3rem;
		width: 100%;
		@media (min-width: 768px) {
			max-width: 30rem;
			height: 4rem;
			font-size: 2rem;
		}
	}

	.validate-input {
		height: 4rem;
		width: 6rem;
		border-radius: 1000px;
		background-color: white;
		border: none;
		outline: none;
		font-size: 1.8rem;
		font-weight: 700;
		cursor: pointer;
		@media (max-width: 767px) {
			height: 3rem;
			width: 6rem;
			font-size: 1.4rem;
		}
	}

	.progress-container {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 1rem;
		margin: 1rem 0;

		@media (min-width: 768px) {
			flex-direction: row;
		}

	}

	.progress {
		background-color: $color-blue-darker;
		color: white;
		width: fit-content;
		border-radius: 1000px;
		padding: 0.8rem 1.2rem;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 1.1rem;
		display: flex;
		align-items: center;
		gap: 1rem;

		.best {
			font-size: 0.7rem;
		}
	}

	.life-remaining {
		display: flex;
		gap: 0.3rem;

		.heart {
			height: 2rem;
			width: 2rem;

			svg {
				height: 100%;
				width: 100%;
			}

			&.loose {
				path {
					fill: none;
				}
			}
		}
	}

	.game-container {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.win-message {
		display: none;

		&.show {
			display: flex;
		}

		flex-direction: column;
		align-items: center;
		font-size: 1.2rem;
		text-transform: uppercase;
		font-weight: 700;
		text-align: left;

		p {
			display: flex;
			align-items: center;
		}

		.bigger {
			font-size: 2em;
			margin-right: 1rem;
		}

		.btn {
			margin-top: 1rem;
		}


		@media (max-width: 767px) {
			br {
				display: none;
			}
		}
	}

	.loose-message {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		display: none;
		width: fit-content;
		max-width: 90%;
		text-transform: uppercase;
		font-weight: 700;
		text-align: center;

		&.show {
			display: flex;
		}

		flex-direction: column;
		align-items: center;

		font-size: 1.2rem;

		.btn {
			margin-top: 1rem;
		}
	}
}
